import comHeader from '@/components/header/header'
import cTop from './components/top'
import cCell from './components/cell'
import { getData } from '@/api/user'
export default {
  name: 'user',
  components: {
    cTop,
    comHeader,
    cCell
  },
  data () {
    return {
      data: {}
    }
  },
  beforeMount () {
    this._onData()
  },
  methods: {
    async _onData () {
      getData().then(({ data: { code, data = {}, msg } }) => {
        if (code === 1) {
          this.data = data
        }
      }).catch((error) => {
        console.log(error)
      })
    }
  }
}



